import * as THREE from "three";
// 
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

// console.log(THREE);

// 3d

// 1
const scene = new THREE.Scene();

// 2
const camera = new THREE.PerspectiveCamera(
  75,
  window.innerWidth / window.innerHeight,
  0.1,
  1000
);

var offsetX = 0
var offsetY = 0


// 
camera.position.set(0, 0, 10);
scene.add(camera);

// 
// 
const planeGeometry = new THREE.PlaneGeometry(12, 12, 20, 20);


const cubeMaterial = new THREE.MeshBasicMaterial(
    {color: 0xAFEEEE, 
    side: THREE.DoubleSide,
    wireframe:true,
    opacity: 0.5},
    );
// 
const plane = new THREE.Mesh(planeGeometry, cubeMaterial);

plane.rotation.x = - Math.PI / 2.5

// 
scene.add(plane);

// 
const renderer = new THREE.WebGLRenderer({
    antialias: true,
    alpha: true
  });
// 
renderer.setSize(window.innerWidth, window.innerHeight);
renderer.setClearColor(0x000000, 0)

// console.log(renderer);
// webglcanvasbody
document.body.appendChild(renderer.domElement);

// // 
// renderer.render(scene, camera);

// 
const controls = new OrbitControls(camera, renderer.domElement);

// 

function updatePlane() {
    let pos = planeGeometry.attributes.position.array
    offsetX += 0.01
    offsetY = 0

    for(let i = 0; i < pos.length; i+=3) {

      pos[i+2] = Math.random() * 0.8

    }
    planeGeometry.attributes.position.needsUpdate = true
  }

var clock = new THREE.Clock();
var FPS = 60
var renderT = 1/FPS;
var timeS = 0;
function render() {
    requestAnimationFrame(render);

    //   render
    var T = clock.getDelta();
    timeS = timeS + T;
    // requestAnimationFramerender60renderer.render
    if (timeS > renderT) {
      // 
      renderer.render(scene, camera); //
      timeS = 0;
      updatePlane()
    }
}

render();